<template>
    <div id="view-employee">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>{{name}}</h4>
            </li>
            <li class="collection-item">Employee ID# {{employee_id}}</li>
            <li class="collection-item">Name {{name}}</li>
            <li class="collection-item">Postion {{position}}</li>
            <li class="collection-item">Joined Date {{join_date}}</li>
            <li class="collection-item">Department {{department}}</li>
            <li class="collection-item">Email Address {{email}}</li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteEmployee" class="btn red">Delete</button>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" 
            class="btn-floating btn-large red"><i class="fa fa-pencil"></i></router-link>
        </div>
    </div>
</template>

<script>
import db from '../firebaseInit'

export default {
    name: "view-employee",
    data(){
        return {
            employee_id: null,
            name: null,
            department: null,
            position: null,
            email: null,
            join_date: null

        }
    },
    beforeRouteEnter(to, from, next){
        alert("beforerouter")
        db.collection("employees")
        .where("employee_id", "==", to.params.employee_id)
        .get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.employee_id = doc.data().employee_id
                    vm.department = doc.data().department
                    vm.position = doc.data().position
                    vm.email = doc.data().email
                    vm.join_date = doc.data().join_date
                    vm.name = doc.data().name
                })
            })
        })
        .catch(err=> console.log(err))

    },
    watch: {
        '$route' : 'fetchData'
    },
    methods: {
        fetchData () {
            alert("fetchdata")
            db.collection("employees")
            .where("employee_id", "==", this.$route.params.employee_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    console.log(querySnapshot)
                    this.employee_id = doc.data().employee_id
                    this.name = doc.data().name
                    this.department = doc.data().department
                    this.email = doc.data().email
                    this.position = doc.data().position
                    this.join_date = doc.data().join_date
                })
            })
        },
        deleteEmployee(){
            if(confirm("Do you want to delete?")){
                db.collection("employees")
                .where("employee_id", "==", this.$route.params.employee_id)
                .get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete();
                        this.$router.push('/')
                    })
                })
            }
        }
    }
}
</script>

