<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Employees</h4></li>
            <li class="collection-item" v-for="employee in employees" v-bind:key="employee.id">
                <div class="chip">{{employee.department}}</div>
                {{employee.employee_id}} : {{employee.name}}

                <router-link class="secondary-content" v-bind:to="{name: 'view-employee', params: {employee_id: employee.employee_id}}"><i class="fa fa-eye"></i></router-link>
            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/employee/new" class="btn-floating btn-large red"><i class="fa fa-plus"></i></router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: "dashboard",
    data(){
        return {
            employees: []
        }
    },
    created() {
        db.collection('employees')
        .orderBy("department")
        .get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                // console.log(doc.data());
                // console.log(doc.id);
                const data = {
                    'id': doc.id,
                    'employee_id': doc.data().employee_id,
                    'department': doc.data().department,
                    'position': doc.data().position,
                    'email': doc.data().email,
                    'join_date': doc.data().join_date,
                    'name': doc.data().name
                }

                this.employees.push(data)
            })
        })
        .catch(err => console.log(err))
    }
}
</script>

