<template>
    <div id="edit-employee">
        <h3>Edit Employee</h3>
        <div class="row">
            <form @submit.prevent="updateEmployee" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="employee_id" placeholder="#" required>
                        <label class="active" for="">Employee ID# </label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" placeholder="Name.." required>
                        <label class="active" for="">Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="department" placeholder="Department.." required>
                        <label class="active" for="">Department</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input class="datepicker" type="text" v-model="join_date" placeholder="Join date.." required>
                        <label class="active" for="">Join Date</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="email" placeholder="Email.." required>
                        <label class="active" for="">Email Address</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="position" placeholder="Position.." required>
                        <label class="active" for="">Position</label>
                    </div>
                </div>

                <button type="submit" class="btn">Save</button>
                <router-link to="/" class="btn grey">Back</router-link>

            </form>
        </div>
    </div>
</template>

<script>
import db from '../firebaseInit'

export default {
    name: "edit-employee",
    data(){
        return {
            employee_id: null,
            name: null,
            department: null,
            position: null,
            email: null,
            join_date: null
        }
    },
    beforeRouteEnter(to, from, next){
        alert("beforerouter")
        db.collection("employees")
        .where("employee_id", "==", to.params.employee_id)
        .get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                alert('hello')
                next(vm => {
                    vm.employee_id = doc.data().employee_id
                    vm.department = doc.data().department
                    vm.position = doc.data().position
                    vm.email = doc.data().email
                    vm.join_date = doc.data().join_date
                    vm.name = doc.data().name
                })
            })
        })
        .catch(err=> console.log(err))

    },
    watch: {
        '$router' : 'fetchData'
    },
    serve: {
        '$router' : 'fetchData'
    },
    methods: {
        fetchData () {
            alert("fetchData")
            db.collection("employees")
            .where("employee_id", "==", this.$route.params.employee_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    console.log(querySnapshot)
                    this.employee_id = doc.data().employee_id
                    this.name = doc.data().name
                    this.department = doc.data().department
                    this.email = doc.data().email
                    this.position = doc.data().position
                    this.join_date = doc.data().join_date
                })
            })
        },
        updateEmployee(){
            db.collection("employees")
            .where("employee_id", "==", this.$route.params.employee_id)
            .get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        employee_id: this.employee_id,
                        name: this.name,
                        department: this.department,
                        join_date: this.join_date,
                        position: this.position,
                        email: this.email
                    })
                    .then(() => {
                        this.$router.push({
                            name: 'view-employee',
                            params: {employee_id: this.employee_id}
                        })
                    })
                })
            })
            .catch(err => console.log(err))
        }
    }
}
</script>

