<template>
  <div id="new-employee">
    <h3>New Employee</h3>
    <div class="row">
      <form @submit.prevent="saveEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input disabled type="text" v-model="employee_id" required>
            <label class="active" for>Employee ID#</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required>
            <label for>Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="department" required>
            <label for>Department</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="join_date" required>
            <label for>Joined Date</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="email" required>
            <label for>Email</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="position" required>
            <label for>Position</label>
          </div>
        </div>

        <button type="submit" class="btn">Save</button>
        <router-link to="/" class="btn grey">Back</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from '../firebaseInit'
import uuid from 'uuid';

export default {
    name: "new-employee",
    data(){
        return {
            employee_id: uuid.v4(),
            name: null,
            department: null,
            position: null,
            email: null,
            join_date: null
        }
    },
    methods: {
        saveEmployee(){
            db.collection("employees")
            .add({
                employee_id: this.employee_id,
                name: this.name,
                department: this.department,
                position: this.position,
                email: this.email,
                join_date: this.join_date
            })
            .then(docRef => {
                this.$router.push("/")
            })
            .catch(err=> console.log(err))
        }
    }
}
</script>

